import React from 'react';
import s from "./styles.module.css";
import {Box, Collapse, Container, List, ListItemButton, ListItemIcon, ListItemText, Typography} from "@mui/material";
import {ExpandLess, ExpandMore} from "@mui/icons-material";
import QuestionMarkIcon from '@mui/icons-material/QuestionMark';

type LocalState = {
    id: number
    open: boolean
    question: string
    answer: string
}

const localState: LocalState[] = [
    {
        id: 1,
        open: false,
        question: '    ?',
        answer: '       .           ,      .'
    },
    {
        id: 2,
        open: false,
        question: '     ?',
        answer: '       30   . ,   ,   .'
    },
    {
        id: 3,
        open: false,
        question: '   ?',
        answer: '      1-2     .     20  30  .'
    },
    {
        id: 4,
        open: false,
        question: '    ?',
        answer: '      .       , ,   ,   .'
    },
    {
        id: 5,
        open: false,
        question: '    ?',
        answer: '           .         '
    },
    {
        id: 6,
        open: false,
        question: '   ?',
        answer: '   99%,      :)'
    },
]

const Questions = () => {

    const [open, setOpen] = React.useState(localState);

    const handleClick = (id: number) => {
        setOpen(open.map(el => el.id === id ? {...el, open: !el.open} : el))
    };

    return (
        <Container maxWidth="lg" className={s.container}>
            <Box className={s.content}>
                <Typography className={s.header} variant={'h4'}>  </Typography>
                {open.map(el => {
                    return (
                        <Box key={el.id}>
                            <ListItemButton onClick={() => handleClick(el.id)} className={s.listItemButton}>
                                <ListItemIcon>
                                    <QuestionMarkIcon/>
                                </ListItemIcon>
                                <ListItemText>{el.question}</ListItemText>
                                {el.open ? <ExpandLess/> : <ExpandMore/>}
                            </ListItemButton>
                            <Collapse in={el.open} timeout="auto" unmountOnExit>
                                <List component="div" disablePadding>
                                    <ListItemButton sx={{pl: 4}}>
                                        <ListItemText>{el.answer}</ListItemText>
                                    </ListItemButton>
                                </List>
                            </Collapse>
                        </Box>
                    )
                })}
            </Box>
        </Container>
    );
};

export default Questions;