import React from 'react';
import {Box, Container, ListItemButton, ListItemIcon, ListItemText, Typography} from "@mui/material";
import s from './styles.module.css'
import DoneIcon from '@mui/icons-material/Done';
import foot from '../../assets/foot.png'
import sizes from '../../assets/sizes.png'

const FootSize = () => {
    return (
        <Container maxWidth="lg" className={s.container}>
            <Box className={s.content}>
                <Typography className={s.header} variant={'h4'}>   ?</Typography>
                <ListItemButton className={s.listItem}>
                    <ListItemIcon>
                        <DoneIcon/>
                    </ListItemIcon>
                    <ListItemText> <b>  </b>  .       , 
                         <b></b>   .</ListItemText>
                </ListItemButton >
                <ListItemButton className={s.listItem}>
                    <ListItemIcon>
                        <DoneIcon/>
                    </ListItemIcon>
                    <ListItemText>    <b>   </b>     
                         <b>( )</b>.</ListItemText>
                </ListItemButton>
                <ListItemButton className={s.listItem}>
                    <ListItemIcon>
                        <DoneIcon/>
                    </ListItemIcon>
                    <ListItemText>           
                          .</ListItemText>
                </ListItemButton>
                <ListItemButton className={s.listItem}>
                    <ListItemIcon>
                        <DoneIcon/>
                    </ListItemIcon>
                    <ListItemText>    ,    <b> 
                         </b>          .</ListItemText>
                </ListItemButton>
                <ListItemButton className={s.listItem}>
                    <ListItemIcon>
                        <DoneIcon/>
                    </ListItemIcon>
                    <ListItemText>  <b>   </b>.     
                          .</ListItemText>
                </ListItemButton>
                <img className={s.img} src={foot} alt={'img'}/>
                <ListItemButton className={s.listItem}>
                    <ListItemIcon>
                        <DoneIcon/>
                    </ListItemIcon>
                    <ListItemText>     .</ListItemText>
                </ListItemButton>
                <img src={sizes} className={s.img} alt={'img'}/>
            </Box>
        </Container>
    );
};

export default FootSize;